import React, { useState } from "react";
import "./Menu.css";
import Input from "./components/Input";
import { Icon } from "@iconify/react";

const Menu = ({ user }) => {
  user = user[0];
  let [activeMenu, setActiveMenu] = useState("home");
  const updateActive = (e) => {
    setActiveMenu(e.target.id)
  };

  return (
    <div className="interface">
      <nav className="navbar">
        <ul className="navbar-items">
          <li
            id="home"
            className={
              activeMenu === "home" ? "navbar-item active" : "navbar-item"
            }
            onClick={updateActive}
          >
            <Icon icon="ant-design:home-filled" />
          </li>
          <li
            id="search"
            className={
              activeMenu === "search" ? "navbar-item active" : "navbar-item"
            }
            onClick={updateActive}
          >
            <Icon icon="akar-icons:search" />
          </li>
          <li id="more" className="navbar-item navbar-item-circle">
            <Icon icon="akar-icons:plus" />
          </li>
          <li
            id="list"
            className={
              activeMenu === "list" ? "navbar-item active" : "navbar-item"
            }
            onClick={updateActive}
          >
            <Icon icon="gg:notes" />
          </li>
          <li
            id="user"
            className={
              activeMenu === "user" ? "navbar-item active" : "navbar-item"
            }
            onClick={updateActive}
          >
            <Icon icon="bx:bxs-user" />
          </li>
        </ul>
      </nav>
      <header>
        <h1 className="title">
          Olá<em>{user.name}</em>
        </h1>
        <div className="card">
          <ul className="item-group">
            <li className="item mercado-1">Extra</li>
            <li className="item mercado-2">Carrefour</li>
            <li className="item mercado-3">Lopes</li>
            <li className="item mercado-4">Walmart</li>
          </ul>
        </div>
        <div className="mercados">
          <Input id="mercados" type="button">
            Mercados Próximos
          </Input>
        </div>
      </header>
    </div>
  );
};

export default Menu;
